















import Vue from "vue";
import Component from "vue-class-component";
import Carousel from "@/components/carousell.vue";
import { Categoria, Movel } from "@/api";

@Component({
  components: {
    Carousel,
  },
})
export default class extends Vue {
  private movel!: Movel;

  private created() {
    const { idCategoria, idProduto } = this.$route.params;
    const categoria = Categoria.getCategoria(parseInt(idCategoria));
    const movel = Movel.getMovel(categoria, parseInt(idProduto));
    if (movel) this.movel = movel;
  }

  get wppLink(): string {
    const now = new Date().getHours();
    let comeco = "Boa noite";
    if (now >= 5 && now < 12) {
      comeco = "Bom dia";
    } else if (now >= 12 && now < 18) {
      comeco = "Boa tarde";
    }
    return (
      "https://wa.me/5511995401715/?text=" +
      encodeURI(
        `${comeco}, gostaria de mais informaes sobre a(o) ${this.movel.nome}`
      )
    );
  }

  get imagesMovel(): string[] {
    return this.movel.imgs.map((x) => "/assets/imgmoveis/" + x);
  }
}
