<template>
  <div>
    <splide
      class="main"
      :options="{
        type: 'loop',
        perPage: 1,
        perMove: 1,
        gap: '1rem',
        pagination: false,
      }"
      ref="mainSlider"
      ><splide-slide v-for="(slide, index) in images" :key="index">
        <img class="images" :src="slide" :alt="index + ' imagem'" />
      </splide-slide>
    </splide>
    <splide
      class="slave"
      :options="{
        type: 'slide',
        rewind: true,
        gap: '.5rem',
        pagination: false,
        fixedWidth: 70,
        fixedHeight: 70,
        arrows: false,
        cover: true,
        focus: 'center',
        isNavigation: true,
        updateOnMove: true,
      }"
      ref="thumbSlider"
    >
      <splide-slide class="link" v-for="(slide, index) in images" :key="index">
        <img :src="slide" :alt="index + ' imagem'" />
      </splide-slide>
    </splide>
  </div>
</template>

<script>
import { Splide, SplideSlide } from "@splidejs/vue-splide";
import "@splidejs/splide/dist/css/themes/splide-default.min.css";

export default {
  props: ["images"],
  components: {
    Splide,
    SplideSlide,
  },
  // eslint-disable-next-line @typescript-eslint/explicit-module-boundary-types
  mounted() {
    this.$refs.mainSlider.sync(this.$refs.thumbSlider.splide);
  },
};
</script>

<style scoped>
.main {
  margin-bottom: 0.5rem;
}

.main >>> .splide__arrow {
  width: 3rem;
  height: 3rem;
  background-color: rgba(0, 0, 0, 0.8);
  transition-duration: 300ms;
  transition-property: background-color;
}

.main >>> .splide__arrow svg {
  fill: white;
  transition-duration: 300ms;
  transition-property: fill;
}

.main >>> .splide__arrow:hover {
  background-color: white;
}

.main >>> .splide__arrow:hover svg {
  fill: black;
}

.images {
  border-radius: 5px;
}

.slave >>> .splide__slide {
  border-radius: 50%;
}

.slave >>> .splide__slide {
  border: none !important;
}

.slave >>> .splide__slide.is-active {
  filter: grayscale();
}

.link {
  cursor: pointer;
}
</style>
